<template>
    <div class="block">
        <h5 class="square-title">
            提案單位近三年執行相關計畫之成效及與本計畫之關聯性與差異性
            <button class="btn btn-teal-dark ms-3" @click="add" type="button"><i class="fas fa-plus"></i>新增計畫目標</button>
        </h5>
        <div class="mt-4" :key="item" v-for="(item,idx) in filteredProjects">
            <div class="border">
                <div class="bg-light-teal p-3 text-gray d-flex align-items-center gap-3">
                    <div class="d-flex gap-2 align-items-center flex-grow-1">
                        <label class="text-nowrap"><span class="text-pink">*</span>相關計畫名稱</label>
                        <div class="flex-grow-1">
                            <input-text :error="errors[`project-${idx}-Title`]" v-model.trim="item.Title" />
                        </div>
                    </div>
                    <div class="d-flex gap-2">
                        <a class="link-teal" @click.prevent="remove(item)" href="#">刪除</a>
                        <a aria-expanded="true" class="link-teal" data-bs-toggle="collapse" href="#workItem2" role="button">收合</a>
                    </div>
                </div>
                <div class="collapse show" id="workItem2">
                    <table class="table align-middle gray-table side-table mb-0">
                        <tbody>
                            <tr>
                                <th><span class="text-pink">*</span> 執行年度</th>
                                <td>
                                    <input-integer :error="errors[`project-${idx}-Year`]" class="w-auto" v-model="item.Year" />
                                </td>
                            </tr>
                            <tr>
                                <th><span class="text-pink">*</span> 執行單位</th>
                                <td>
                                    <input-text :error="errors[`project-${idx}-OrgName`]" v-model.trim="item.OrgName" />
                                </td>
                            </tr>
                            <tr>
                                <th><span class="text-pink">*</span> 計畫經費(元)</th>
                                <td>
                                    <input-integer :error="errors[`project-${idx}-Amount`]" class="w-auto" v-model="item.Amount" />
                                </td>
                            </tr>
                            <tr>
                                <th><span class="text-pink">*</span> 內容摘述</th>
                                <td>
                                    <input-textarea :error="errors[`project-${idx}-Description`]" rows="4" v-model.trim="item.Description" />
                                </td>
                            </tr>
                            <tr>
                                <th><span class="text-pink">*</span> 執行效益</th>
                                <td>
                                    <input-textarea :error="errors[`project-${idx}-Benefit`]" rows="4" v-model.trim="item.Benefit" />
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
    <div class="block-bottom bg-light-teal">
        <button class="btn btn-outline-teal me-3" @click="save()" type="button">暫存</button>
        <button class="btn btn-teal" @click="save(true)" type="button"><i class="fas fa-check"></i>完成本頁，下一步</button>
    </div>
</template>

<script setup>
    const props = defineProps({
        id: { type: Number }
    });

    const errors = ref({});
    const projects = ref([]);

    const filteredProjects = computed(() => projects.value.filter((item) => !item.Deleted));

    const add = () => projects.value.push({});

    const remove = (item) => item.Deleted = true;

    const save = (submit) => {
        if (submit && !verify()) {
            nextTick(() => document.querySelector(".invalid")?.scrollIntoView({ behavior: "smooth", block: "center" }));
            return;
        }

        const data = {
            ID: props.id,
            Projects: projects.value,
            Submit: submit ? "true" : "false"
        };

        api.culture("saveRelatedProject", data).subscribe(() => {
            if (submit) {
                window.location.href = `Attachment.aspx?ID=${props.id}`;
            }
        });
    };

    const verify = () => {
        errors.value = {};

        const rules = {
            Title: "相關計畫名稱",
            Year: "執行年度",
            OrgName: "執行單位",
            Amount: { presence: { allowEmpty: false, message: `^請輸入計畫經費` }, numericality: { greaterThan: 0, message: "^計畫經費不可為 0" } },
            Description: "內容摘述",
            Benefit: "執行效益"
        };

        projects.value.forEach((project, index) => Object.assign(errors.value, validateData(project, rules, `project-${index}-`)));

        return !Object.keys(errors.value).length;
    };

    onMounted(() => {
        rxjs.forkJoin([
            api.culture("getRelatedProject", { ID: props.id })
        ]).subscribe((result) => {
            const data = result[0];

            projects.value = data.Projects;

            useProgressStore().culture = data.Project.FormStep;
        });
    });
</script>
