import CultureApplication from "./components/CultureApplication.vue";
import CultureAttachment from "./components/CultureAttachment.vue";
import CultureFunding from "./components/CultureFunding.vue";
import CultureOther from "./components/CultureOther.vue";
import CultureProgressBar from "./components/CultureProgressBar.vue";
import CultureWorkSchedule from "./components/CultureWorkSchedule.vue";
import InputBoolean from "./components/InputBoolean.vue";
import InputInteger from "./components/InputInteger.vue";
import InputMonth from "./components/InputMonth.vue";
import InputRadioGroup from "./components/InputRadioGroup.vue";
import InputSelect from "./components/InputSelect.vue";
import InputText from "./components/InputText.vue";
import InputTextarea from "./components/InputTextarea.vue";
import InputTwDate from "./components/InputTwDate.vue";

const components = {
    CultureApplication,
    CultureAttachment,
    CultureFunding,
    CultureOther,
    CultureProgressBar,
    CultureWorkSchedule,
    InputBoolean,
    InputInteger,
    InputMonth,
    InputRadioGroup,
    InputSelect,
    InputText,
    InputTextarea,
    InputTwDate
};

export default {
    install: (app) => {
        for (const name in components) {
            app.component(name, components[name]);
        }
    }
};
