<template>
    <input class="form-control text-end" :type="type" v-bind="$attrs" v-model="model" />
    <div class="invalid" v-if="error">{{ error }}</div>
</template>

<script setup>
    const model = defineModel();

    defineOptions({ inheritAttrs: false });

    defineProps({
        error: { type: String },
        type: { default: "text", type: String }
    });

    watch(model, (value) => {
        if (value) {
            const num = (parseInt(value) || 0).toString();

            if (value.toString() !== num) {
                model.value = num;
            }
        }
    });
</script>
