<template>
    <div class="application-step">
        <div class="step-item" :class="{ active: item.step <= progress.culture }" @click="goto(item)" :key="item" role="button" v-for="(item) in steps">
            <div class="step-content">
                <div class="step-label">{{ item.title }}</div>
                <div class="step-status edit" v-if="item.step === step">編輯中</div>
                <div class="step-status" v-else-if="item.step < progress.culture">已完成</div>
            </div>
        </div>
    </div>
</template>

<script setup>
    const progress = useProgressStore();

    const props = defineProps({
        id: { type: Number },
        review: { default: false, type: Boolean },
        step: { required: true, type: Number }
    });

    const steps = [
        { step: 1, title: "申請表／計畫書", url: "Application" },
        { step: 2, title: "期程／工作項目／查核", url: "WorkSchedule" },
        { step: 3, title: "經費", url: "Funding" },
        { step: 4, title: "其他", url: "Other" },
        { step: 5, title: "上傳附件／提送申請", url: "Attachment" }
    ];

    const goto = (item) => {
        if (props.review) {
            emit("click", item.step);
        } else if (item.step <= progress.culture) {
            location.href = `${item.url}.aspx?ID=${props.id}`;
        }
    };

    const emit = defineEmits(["click"]);
</script>
