<template>
    <div class="block">
        <h5 class="square-title">期程</h5>
        <div class="mt-4">
            <table class="table align-middle gray-table side-table">
                <tbody>
                    <tr>
                        <th><span class="text-pink">*</span> 計畫期程</th>
                        <td>
                            <div class="d-flex align-items-center gap-2">
                                <div class="input-group w-auto">
                                    <input-tw-date class="w-auto" v-model="form.StartTime" />
                                    <span class="input-group-text">至</span>
                                    <input-tw-date class="w-auto" v-model="form.EndTime" />
                                </div>
<span class="text-teal-dark">(期程不可超過 115/04/30)</span>
                            </div>
                            <div class="invalid" v-if="errors.StartTime || errors.EndTime">{{ errors.StartTime || errors.EndTime }}</div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <h5 class="square-title">
            計畫目標／工作項目與步驟／工作進度與應檢核工作
            <button class="btn btn-teal-dark ms-3" @click="addGoal" type="button"><i class="fas fa-plus"></i>新增計畫目標</button>
        </h5>
        <div class="mt-4">
            <div class="block-form mt-3" :key="goal" v-for="(goal,idx) in filteredGoals">
                <button class="btn btn-teal del" @click="removeGoal(goal)" type="button" v-if="filteredGoals.length > 1">刪除</button>
                <div class="d-flex flex-column gap-2">
                    <label class="text-teal-dark">計畫目標</label>
                    <div>
                        <input-text :error="errors[`goal-${idx}-Title`]" placeholder="例:挖掘消失的水路航道" v-model.trim="goal.Title" />
                    </div>
                </div>
                <div class="mt-4 d-flex flex-column gap-2">
                    <label class="text-teal-dark">預期效益（含量化或質化說明）</label>
                    <div>
                        <input-textarea :error="errors[`goal-${idx}-Content`]" rows="4" v-model.trim="goal.Content" />
                    </div>
                </div>
                <div class="d-flex flex-column gap-3 mt-4">
                    <div class="border" :key="item" v-for="(item,idx1) in goal.Items.filter((item) => !item.Deleted)">
                        <div class="bg-light-teal p-3 text-gray d-flex align-items-center gap-3">
                            <div class="d-flex gap-2 align-items-center flex-grow-1">
                                <label class="text-nowrap"><span class="text-pink">*</span> 重要工作項目</label>
                                <div class="flex-grow-1">
                                    <input-text :error="errors[`item-${idx}-${idx1}-Title`]" placeholder="例:調查研究" v-model.trim="item.Title" />
                                </div>
                            </div>
                            <div class="d-flex gap-2">
                                <a class="link-teal" @click.prevent="removeItem(item)" href="#" v-if="goal.Items.filter((item) => !item.Deleted).length > 1">刪除</a>
                                <a aria-expanded="true" class="link-teal" data-bs-toggle="collapse" href="#workItem" role="button">收合</a>
                            </div>
                        </div>
                        <div class="collapse show" id="workItem">
                            <table class="table align-middle gray-table side-table mb-0">
                                <tbody>
                                    <tr>
                                        <th><span class="text-pink">*</span> 實施步驟及起迄月份</th>
                                        <td>
                                            <ul class="item-list">
                                                <li class="step-item section-wrapper" :key="step" v-for="(step,idx2) in item.Steps.filter((step) => !step.Deleted)">
                                                    <div>{{ String.fromCharCode(65 + idx2) }}</div>
                                                    <div>
                                                        <input-text :error="errors[`step-${idx}-${idx1}-${idx2}-Title`]" placeholder="例:進行口訪紀錄" v-model.trim="step.Title" />
                                                    </div>
                                                    <div>
                                                        <div class="input-group">
                                                            <input-month :empty-value="0" v-model="step.Begin" />
                                                            <span class="input-group-text">至</span>
                                                            <input-month :empty-value="0" v-model="step.End" />
                                                        </div>
                                                        <div class="invalid" v-if="errors[`step-${idx}-${idx1}-${idx2}-Begin`] || errors[`step-${idx}-${idx1}-${idx2}-End`]">{{ errors[`step-${idx}-${idx1}-${idx2}-Begin`] || errors[`step-${idx}-${idx1}-${idx2}-End`] }}</div>
                                                    </div>
                                                    <div class="d-flex gap-2">
                                                        <button
                                                            class="btn btn-sm btn-teal-dark m-0"
                                                            @click="removeStep(step)"
                                                            type="button"
                                                            v-if="item.Steps.filter((step) => !step.Deleted).length > 1"
                                                        >
                                                            <i class="fas fa-trash-alt"></i>
                                                        </button>
                                                        <button class="btn btn-sm btn-teal-dark m-0 add-btn" @click="addStep(item)" type="button"><i class="fas fa-plus"></i></button>
                                                    </div>
                                                </li>
                                            </ul>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th><span class="text-pink">*</span> 績效指標</th>
                                        <td>
                                            <input-text :error="errors[`item-${idx}-${idx1}-Indicator`]" placeholder="例:完成口訪資料1篇" v-model.trim="item.Indicator" />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th><span class="text-pink">*</span> 工作進度</th>
                                        <td>
                                            <ul class="item-list">
                                                <li>
                                                    <div>
                                                        <div class="fs-16 text-gray">工作進度達 50% 應檢核工作</div>
                                                        <div class="row g-2 align-items-center mt-2 section-wrapper" :key="schedule" v-for="(schedule,idx2) in item.Schedules.filter((schedule) => schedule.Type === 1 && !schedule.Deleted)">
                                                            <div class="col-2">
                                                                <input-month :error="errors[`schedule-${idx}-${idx1}-1-${idx2}-Month`]" :empty-value="0" v-model="schedule.Month" />
                                                            </div>
                                                            <div class="col-8">
                                                                <input-select :empty-value="0" :error="errors[`schedule-${idx}-${idx1}-2-${idx2}-StepID`]" :options="item.Steps.filter((step) => !step.Deleted)" v-model="schedule.StepID">
                                                                    <template #default="{ index, option }">
                                                                        <option :value="option.ID">{{ String.fromCharCode(65 + index) }}.{{ option.Title }}</option>
                                                                    </template>
                                                                </input-select>
                                                            </div>
                                                            <div class="col-2 d-flex gap-2">
                                                                <button
                                                                    class="btn btn-sm btn-teal-dark m-0"
                                                                    @click="removeSchedule(schedule)"
                                                                    type="button"
                                                                    v-if="item.Schedules.filter((schedule) => schedule.Type === 1 && !schedule.Deleted).length > 1"
                                                                >
                                                                    <i class="fas fa-trash-alt"></i>
                                                                </button>
                                                                <button class="btn btn-sm btn-teal-dark m-0 add-btn" @click="addSchedule(item, 1)" type="button"><i class="fas fa-plus"></i></button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div>
                                                        <div class="fs-16 text-gray">工作進度達 100% 應檢核工作</div>
                                                        <div class="row g-2 align-items-center mt-2 section-wrapper" :key="schedule" v-for="(schedule,idx2) in item.Schedules.filter((schedule) => schedule.Type === 2 && !schedule.Deleted)">
                                                            <div class="col-2">
                                                                <input-month :error="errors[`schedule-${idx}-${idx1}-2-${idx2}-Month`]" :empty-value="0" v-model="schedule.Month" />
                                                            </div>
                                                            <div class="col-8">
                                                                <input-select :empty-value="0" :error="errors[`schedule-${idx}-${idx1}-2-${idx2}-StepID`]" :options="item.Steps.filter((step) => !step.Deleted)" v-model="schedule.StepID">
                                                                    <template #default="{ index, option }">
                                                                        <option :value="option.ID">{{ String.fromCharCode(65 + index) }}.{{ option.Title }}</option>
                                                                    </template>
                                                                </input-select>
                                                            </div>
                                                            <div class="col-2 d-flex gap-2">
                                                                <button
                                                                    class="btn btn-sm btn-teal-dark m-0"
                                                                    @click="removeSchedule(schedule)"
                                                                    type="button"
                                                                    v-if="item.Schedules.filter((schedule) => schedule.Type === 2 && !schedule.Deleted).length > 1"
                                                                >
                                                                    <i class="fas fa-trash-alt"></i>
                                                                </button>
                                                                <button class="btn btn-sm btn-teal-dark m-0 add-btn" @click="addSchedule(item, 2)" type="button"><i class="fas fa-plus"></i></button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </li>
                                            </ul>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <button class="btn btn-teal mt-3" @click="addItem(goal)" type="button">新增工作項目</button>
            </div>
        </div>
    </div>
    <div class="block-bottom bg-light-teal">
        <button class="btn btn-outline-teal me-3" @click="save()" type="button">暫存</button>
        <button class="btn btn-teal" @click="save(true)" type="button"><i class="fas fa-check"></i>完成本頁，下一步</button>
    </div>
</template>

<style scoped>
    .section-wrapper:not(:last-child) .add-btn {
        display: none;
    }
</style>

<script setup>
    const props = defineProps({
        id: { type: Number }
    });

    const errors = ref({});
    const form = ref({});
    const goals = ref([]);

    const filteredGoals = computed(() => goals.value.filter((goal) => !goal.Deleted));

    const addGoal = () => {
        const goal = { Items: [] };

        addItem(goal);

        goals.value.push(goal);
    };

    const addItem = (goal) => {
        const item = { Steps: [], Schedules: [] };

        addStep(item);
        addStep(item);
        addStep(item);
        addSchedule(item, 1);
        addSchedule(item, 2);

        goal.Items.push(item);
    };

    const addSchedule = (item, type) => item.Schedules.push({ Type: type, Month: 0, StepID: 0 });

    const addStep = (item) => item.Steps.push({ Begin: 0, End: 0 });

    const removeGoal = (goal) => goal.Deleted = true;

    const removeItem = (item) => item.Deleted = true;

    const removeSchedule = (schedule) => schedule.Deleted = true;

    const removeStep = (step) => step.Deleted = true;

    const save = (submit) => {
        if (submit && !verify()) {
            nextTick(() => document.querySelector(".invalid")?.scrollIntoView({ behavior: "smooth", block: "center" }));
            return;
        }

        const data = {
            Project: form.value,
            Goals: goals.value,
            Submit: submit ? "true" : "false"
        };

        api.culture("saveWorkSchedule", data).subscribe(() => {
            if (submit) {
                window.location.href = `Funding.aspx?ID=${props.id}`;
            }
        });
    };

    const verify = () => {
        const rules = {
            StartTime: "計畫期程",
            EndTime: "計畫期程"
        };

        errors.value = validateData(form.value, rules);

        const goalRules = {
            Title: "計畫目標",
            Content: "預期效益"
        };

        const itemRules = {
            Title: "重要工作項目",
            Indicator: "績效指標"
        };

        const stepRules = {
            Title: "實施步驟",
            Begin: { numericality: { greaterThan: 0, message: "^請選擇起迄月份" } },
            End: { numericality: { greaterThan: 0, message: "^請選擇起迄月份" } }
        };

        const scheduleRules = {
            Month: { numericality: { greaterThan: 0, message: "^請選擇月份" } },
            StepID: { numericality: { greaterThan: 0, message: "^請選擇實施步驟" } }
        };

        goals.value.filter((goal) => !goal.Deleted).forEach((goal, idx) => {
            Object.assign(errors.value, validateData(goal, goalRules, `goal-${idx}-`));

            goal.Items.filter((item) => !item.Deleted).forEach((item, idx1) => {
                Object.assign(errors.value, validateData(item, itemRules, `item-${idx}-${idx1}-`));

                item.Steps.filter((step) => !step.Deleted).forEach((step, idx2) => Object.assign(errors.value, validateData(step, stepRules, `step-${idx}-${idx1}-${idx2}-`)));
                item.Schedules.filter((schedule) => schedule.Type === 1 && !schedule.Deleted).forEach((schedule, idx2) => Object.assign(errors.value, validateData(schedule, scheduleRules, `schedule-${idx}-${idx1}-1-${idx2}-`)));
                item.Schedules.filter((schedule) => schedule.Type === 2 && !schedule.Deleted).forEach((schedule, idx2) => Object.assign(errors.value, validateData(schedule, scheduleRules, `schedule-${idx}-${idx1}-2-${idx2}-`)));
            });
        });

        return !Object.keys(errors.value).length;
    };

    onMounted(() => {
        rxjs.forkJoin([
            api.culture("getWorkSchedule", { ID: props.id })
        ]).subscribe((result) => {
            const data = result[0];

            form.value = data.Project;
            goals.value = data.Goals;

            if (!goals.value.length) {
                addGoal();
            }

            useProgressStore().culture = form.value.FormStep;
        });
    });
</script>
