<template>
    <input class="form-control" :type="type" v-bind="$attrs" v-model="model" />
    <div class="invalid" v-if="error">{{ error }}</div>
</template>

<script setup>
    const model = defineModel();

    defineOptions({ inheritAttrs: false });

    defineProps({
        error: { type: String },
        type: { default: "text", type: String }
    });
</script>
