<template>
    <textarea class="form-control" v-bind="$attrs" v-model="model"></textarea>
    <div class="fs-14 text-gray mt-2" v-if="maxLength">
        已輸入 <span class="text-pink">{{ model?.length }}</span> 個字數(限{{ maxLength }}字數)
    </div>
    <div class="invalid" v-if="error">{{ error }}</div>
</template>

<script setup>
    const model = defineModel();

    const props = defineProps({
        error: { type: String },
        maxLength: { type: Number }
    });

    defineOptions({ inheritAttrs: false });

    if (props.maxLength) {
        watch(model, () => {
            if (model.value?.length > props.maxLength) {
                model.value = model.value.substring(0, props.maxLength);
            }
        });
    }
</script>
